<template>
  <div class="zj3">
    <ul class="u1">
      <li class="l1">
        <div></div>
        <el-input size="small" placeholder v-model="dh" style="width:50%"></el-input>
      </li>

      <li class="l2">
        <div>id</div>
        <el-input size="small" placeholder v-model="id" style="width:50%"></el-input>
      </li>

      <li class="l3">
        <div></div>
        <el-select v-model="czfs" :placeholder="options[0].label" size="mini" style="width:52%">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.label"
          ></el-option>
        </el-select>
      </li>
      <li class="l4">
        <span></span>
        <el-input size="small" placeholder v-model="input1" style="width:30%"></el-input>
        <span></span>
        <el-input size="small" placeholder v-model="input2" style="width:30%"></el-input>
      </li>
      <el-button size="small" style="width:105px" @click="cx"></el-button>
      <el-button size="small" style="width:105px" @click="exportExcel">excel</el-button>
    </ul>

    <el-table
      :data="tableData"
      border
      style="width: 57.35%"
      show-summary
      :summary-method="getSummaries"
    >
      <el-table-column prop="dh" label="id" width="180"></el-table-column>
      <el-table-column prop="userid" label="" width="140"></el-table-column>
      <el-table-column prop="name" label="" width="200"></el-table-column>
      <el-table-column prop="czfs" label="" width="120"></el-table-column>
      <el-table-column prop="num" label="" width="100"></el-table-column>
      <el-table-column prop="txtime" label="" width="200"></el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  data() {
    return {
      options: [
        {
          value: "1",
          label: ""
        },
        {
          value: "2",
          label: ""
        }
      ],

      tableData: [
        {
          dh: "111",
          userid: "1122",
          name: "",
          czfs: "",
          num: 50,
          txtime: "2019-10-17 1600"
        },
        {
          dh: "222",
          userid: "2233",
          name: "",
          czfs: "",
          num: 500,
          txtime: "2019-10-17 2000"
        }
      ],
      dh: "",
      id: "",
      czfs: "",
      input1: "",
      input2: "",
      num: "",
      list: {}
    };
  },

  methods: {
    cx() {
      if (
        this.dh.length == 0 ||
        this.id.length == 0 ||
        this.czfs.length == 0 ||
        this.input1.length == 0 ||
        this.input2.length == 0
      ) {
        alert("");
      } else {
        let sj = {
          dh: this.dh,
          id: this.id,
          czfs: this.czfs,
          name1: this.input1,
          name2: this.input2
        };

        window.console.log(sj);
        this.id = "";
        this.dh = "";
        this.dkzt = "";
        this.input1 = "";
        this.input2 = "";

        let price = 0;
        this.tableData.map(item => {
          return (price += item.num);
        });

        this.num = price;
      }
    },
    getSummaries(param) {
      const { columns } = param;

      const sums = [];
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = "";
          return;
        }
        //this.closeList
        switch (column.property) {
          case "num":
            sums[index] = this.num;
            break;
          default:
            break;
        }
      });

      return sums;
    },

    exportExcel() {
      require.ensure([], () => {
        const {
          export_json_to_excel
        } = require("../../../excel/Export2Excel.js");
        const tHeader = [
          "id",
          "",
          "",
          "",
          "",
          ""
        ];
        // Excel
        const filterVal = ["dh", "userid", "name", "czfs", "num", "txtime"];
        // indexnickNamenametableData
        const list = this.tableData; //datatableDatalist
        const data = this.formatJson(filterVal, list);
        export_json_to_excel(tHeader, data, "");
      });
    },

    formatJson(filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => v[j]));
    }
  }
};
</script>
<style lang="scss" scoped>
.zj3 {
  li {
    list-style: none;
  }
  .u1 {
    display: flex;
    width: 57.2%;

    .l1,
    .l2 {
      display: flex;
      div {
        font-size: 1rem;
        line-height: 2rem;
        margin-right: 0.4375rem;
      }
    }

    .l3 {
      display: flex;
      width: 301px;
      margin-right: 7px;
      div {
        font-size: 1rem;
        line-height: 2rem;
        margin-right: 0.4375rem;
        width: 4.0625rem;
      }
    }
    .l4 {
      span {
        font-size: 1rem;
        line-height: 2rem;
        margin-right: 0.625rem;
        margin-left: 0.625rem;
      }
    }
  }

  .el-table {
    margin-left: 2.5rem;
  }
}
</style>