<template>
  <div class="yh3">
    <ul class="u1">
      <li class="l1">
        <div class="d1">id</div>
        <el-input size="small" placeholder v-model="id" style="width:103px"></el-input>
      </li>
      <li class="l2">
        <div class="d1"></div>
        <el-input size="small" placeholder v-model="sj" style="width:103px"></el-input>
      </li>

      <li class="l5">
        <span></span>
        <el-input size="small" placeholder v-model="first" style="width:103px"></el-input>
        <span></span>
        <el-input size="small" placeholder v-model="end" style="width:103px"></el-input>
      </li>

      <li class="l3">
        <div class="d1"></div>
        <el-select v-model="xfd" size="mini" style="width:75px">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.label"
          ></el-option>
        </el-select>
      </li>
      <li class="l4" style="width:80px">
        <el-button size="small" style="width:100%" @click="cx"></el-button>
        <el-button size="small" style="width:100%" @click="exportExcel">excel</el-button>
      </li>
    </ul>
    <el-table
      :data="tableData"
      border
      style="width:54.3%; margin-left: 40px"
      show-summary
      :summary-method="getSummaries"
    >
      <el-table-column prop="id" label="id" width="80"></el-table-column>
      <el-table-column prop="sj" label="" width="150"></el-table-column>
      <el-table-column prop="username" label="" width="150"></el-table-column>
      <el-table-column prop="money" label="" width="80"></el-table-column>
      <el-table-column prop="xfd" label="" width="80"></el-table-column>
      <el-table-column prop="lw" label="" width="150"></el-table-column>
      <el-table-column prop="num" label="" width="50"></el-table-column>
      <el-table-column prop="time" label="" width="150"></el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  data() {
    return {
      options: [
        {
          value: "1",
          label: ""
        },
        {
          value: "2",
          label: ""
        },
        {
          value: "3",
          label: ""
        }
      ],
      tableData: [
        {
          id: 112233,
          sj: 18617271159,
          username: "",
          money: 100,
          xfd: "",
          lw: "",
          num: 1,
          time: "2019-10-12 18:00"
        }
      ],
      xfd: "",
      sj: "",
      id: "",
      first: "",
      end: "",
      money: "",
      num: ""
    };
  },

  methods: {
    cx() {
      if (this.id.length === 0 || this.sj.length === 0) {
        alert("");
      } else {
        let sj = {
          id: this.id,
          sj: this.sj,
          firsttime: this.first,
          endtime: this.end,
          xfd: this.xfd
        };
        window.console.log(sj);
        this.id = "";
        this.sj = "";
        this.first = "";
        this.end = "";

        let price = 0;
        this.tableData.map(item => {
          return (price += item.money);
        });
        this.money = price;

        let num = 0;
        this.tableData.map(item => {
          return (num += item.num);
        });
        this.num = num;
      }
    },
    getSummaries(param) {
      const { columns } = param;

      const sums = [];
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = "";
          return;
        }
        //this.closeList
        switch (column.property) {
          case "money":
            sums[index] = this.money;
            break;
          case "num":
            sums[index] = this.num;
            break;
          default:
            break;
        }
      });

      return sums;
    },
    exportExcel() {
      require.ensure([], () => {
        const {
          export_json_to_excel
        } = require("../../../excel/Export2Excel.js");
        const tHeader = [
          "id",
          "",
          "",
          "",
          "",
          "",
          "",
          ""
        ];
        // Excel
        const filterVal = [
          "id",
          "sj",
          "username",
          "money",
          "xfs",
          "lw",
          "num",
          "time"
        ];
        // indexnickNamenametableData
        const list = this.tableData; //datatableDatalist
        const data = this.formatJson(filterVal, list);
        export_json_to_excel(tHeader, data, "");
      });
    },

    formatJson(filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => v[j]));
    }
  }
};
</script>
<style lang="scss" scoped>
.yh3 {
  li {
    list-style: none;
  }
  .u1 {
    display: flex;
    padding-left: 0;
    padding-bottom: 30px;

    li {
      display: flex;
    }
    .l1 {
      margin-right: 30px;
      .d1 {
        font-size: 16px;
        line-height: 32px;
        margin-right: 7px;
      }
    }

    .l2 {
      margin-right: 100px;
      .d1 {
        font-size: 16px;
        line-height: 32px;
        margin-right: 7px;
      }
    }

    .l3 {
      margin-right: 100px;
      .d1 {
        font-size: 16px;
        line-height: 28px;
        margin-right: 7px;
      }
    }

    .l5 {
      display: flex;
      margin-right: 30px;
      width: 270px;
      span {
        font-size: 1rem;
        line-height: 2rem;
        margin-left: 7px;
        margin-right: 7px;
      }
    }
  }
}
</style>