<template>
  <div class="zd2">
    <div class="top">
      <ul class="u1">
        <li>
          <span>id</span>
          <el-input size="small" v-model="userid" style="width:50%"></el-input>
        </li>

        <li>
          <span>id</span>
          <el-input size="small" v-model="fjid" style="width:50%"></el-input>
        </li>

        <li>
          <span></span>
          <el-input size="small" v-model="time1" style="width:30%"></el-input>
          <span></span>
          <el-input size="small" v-model="time2" style="width:30%"></el-input>
        </li>
        <el-button size="small" @click="buttom"></el-button>
        <el-button size="small" @click="exportExcel"></el-button>
      </ul>
    </div>

    <el-table
      :data="tableData"
      border
      style="width:53%"
      show-summary
      :summary-method="getSummaries"
    >
      <el-table-column prop="userid" label="id" width="100" align="center"></el-table-column>
      <el-table-column prop="fjid" label="id" width="100" align="center"></el-table-column>
      <el-table-column prop="je" label="" width="150" align="center"></el-table-column>
      <el-table-column prop="time" label="" width="200" align="center"></el-table-column>
      <el-table-column prop="jp" label="" width="200" align="center"></el-table-column>
      <el-table-column prop="price" label="" width="120" align="center"></el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  data() {
    return {
      tableData: [
        {
          userid: "123455",
          fjid: "112233",
          je: 123456,
          time: "2019-10-22 17:00",
          jp: "",
          price: 9999
        },
        {
          userid: "123455",
          fjid: "112233",
          je: 123456,
          time: "2019-10-22 17:00",
          jp: "",
          price: 9999
        },
        {
          userid: "123455",
          fjid: "112233",
          je: 123456,
          time: "2019-10-22 17:00",
          jp: "",
          price: 9999
        },
        {
          userid: "123455",
          fjid: "112233",
          je: 123456,
          time: "2019-10-22 17:00",
          jp: "",
          price: 9999
        }
      ],
      userid: "",
      fjid: "",
      time1: "",
      time2: "",
      je: "",
      price: ""
    };
  },

  methods: {
    buttom() {
      if (
        this.userid.length === 0 ||
        this.fjid.length === 0 ||
        this.time.length === 0
      ) {
        alert("");
      } else {
        let value = {
          userid: this.userid,
          fjid: this.fjid,
          time: this.time
        };
        window.console.log(value);
        this.userid = "";
        this.fjid = "";
        this.time = "";

        let je = 0;
        this.tableData.map(item => {
          return (je += item.je);
        });
        this.je = je;

        let price = 0;
        this.tableData.map(item => {
          return (price += item.price);
        });
        this.price = price;
      }
    },
    getSummaries(param) {
      const { columns } = param;

      const sums = [];
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = "";
          return;
        }
        //this.closeList
        switch (column.property) {
          case "je":
            sums[index] = this.je;
            break;
          case "price":
            sums[index] = this.price;
            break;
          default:
            break;
        }
      });

      return sums;
    },
    exportExcel() {
      require.ensure([], () => {
        const {
          export_json_to_excel
        } = require("../../../excel/Export2Excel.js");
        const tHeader = [
          "id",
          "id",
          "",
          "",
          "",
          ""
        ];
        // Excel
        const filterVal = ["userid", "fjid", "je", "time", "jp", "price"];
        // indexnickNamenametableData
        const list = this.tableData; //datatableDatalist
        const data = this.formatJson(filterVal, list);
        export_json_to_excel(tHeader, data, "");
      });
    },

    formatJson(filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => v[j]));
    }
  }
};
</script>
<style lang="scss" scoped>
li {
  list-style: none;
}

.zd2 {
  .top {
    padding-bottom: 30px;
    .u1 {
      display: flex;
      justify-content: flex-start;
      padding-left: 0px;
      width: 53%;
      li {
        margin-right: 1.25rem;
        display: flex;
        span {
          font-size: 1rem;
          line-height: 32px;
          margin-right: 7px;
          margin-left: 7px;
        }
      }
      .el-button {
        margin-left: 0.625rem;
      }
    }
  }
}
</style>